body {
    font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
}

@font-face {
    font-family: ArapeySubset;
    font-style: normal;
    font-weight: 400;
    unicode-range: U+20-7E;
    font-display: swap;
    src: url("data:application/x-font-woff;charset=utf-8;base64,") format("woff");
}

@font-face {
    font-family: Arapey;
    font-style: normal;
    font-weight: 400;
    font-display: fallback;
    src: url(../fonts/arapey-regular.woff2) format("woff2"), url(../fonts/arapey-regular.woff) format("woff"), url(../fonts/arapey-regular.ttf) format("truetype")
}

@font-face {
    font-family: ArapeyItalic;
    font-style: italic;
    font-weight: 400;
    font-display: fallback;
    src: url(../fonts/arapey-italic.woff2) format("woff2"), url(../fonts/arapey-italic.woff) format("woff"), url(../fonts/arapey-italic.ttf) format("truetype")
}

.mainheader h1 {
    font-size: 1.4em;
}

.fonts-stage-1 .title, .fonts-stage-1 .mainheader h1 {
    font-family: ArapeySubset, serif;
    font-weight: 400;
    font-style: normal;
}

.fonts-stage-2 .title, .fonts-stage-2 .mainheader h1 {
    font-family: Arapey, serif;
    text-transform: lowercase;
}

